@import "http://fonts.googleapis.com/css?family=Patua+One|Inika";
html {
  box-sizing: border-box; }

*, *::after, *::before {
  box-sizing: inherit; }

body {
  background-color: #3879d9; }

header {
  background-color: #87ed43; }

section {
  max-width: 68em;
  margin-left: auto;
  margin-right: auto;
  display: block;
  text-align: center;
  color: #ebebeb;
  background-color: #330000; }

section::after {
  clear: both;
  content: "";
  display: table; }

.first {
  float: left;
  display: block;
  margin-right: 2.35765%;
  width: 48.82117%;
  background-color: #7ebfff;
  border: 1px solid #ebebeb; }

.first:last-child {
  margin-right: 0; }

.second {
  float: left;
  display: block;
  margin-right: 2.35765%;
  width: 48.82117%;
  background-color: #7ebfff;
  border: 1px solid #ebebeb; }

.second:last-child {
  margin-right: 0; }
